<template>
  <div class="same_width_heigth plant">
    <div
      class="wordcloud"
      @mouseenter="intoWordcloud"
      @mousemove="mouseMove"
      @mouseleave="mouseLeave"
      ref="plantCloud"
    >
      <img src="../../assets/plants.png" />
    </div>
    <pieechart class="echart" :pieData="pieData"></pieechart>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "plants_world_colud",
  computed: {
    ...mapState({
      pieData: (state) => state.plant.pageData,
    }),
  },
  methods: {
    intoWordcloud(event) {
      this.initWordcloud = event.clientX;
      console.log(this.initWordcloud);
    },
    mouseMove(event) {
      let moveDistance = this.initWordcloud - event.clientX;
      this.$refs.plantCloud.style.transform = `translateX(${moveDistance}px)`;
    },
    // 鼠标离开
    mouseLeave() {
      this.$refs.plantCloud.style.transform = `translateX(0px)`;
    },
  },
  mounted() {
    this.$store.dispatch("plant/sendPlantRequest", { page: 1, size: 50 });
  },
};
</script>

<style scoped>
.plant {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  overflow: hidden;
}
.wordcloud {
  height: 200px;
}
.wordcloud > img {
  transform: translateX(-35%);
}
.wordcloud,
.echart {
  width: calc(100% - 10px);
  vertical-align: top;
  margin: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  padding: 10px;
  border-radius: 8px;
  box-sizing: border-box;
}
</style>